@import url(https://fonts.googleapis.com/css?family=Arimo&display=swap);
.button {
  padding: 4px 8px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.button--primary {
  color: #FFF !important;
  background-color: var(--primary-color);
  border-radius: 4px;
}
.select {
  display: inline-block;
  padding: 8px;
  position: relative;
}

.select__display {
  display: grid;
  grid-template-columns: -webkit-min-content -webkit-min-content;
  grid-template-columns: min-content min-content;
  grid-gap: 4px;
}

.select__input {
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.menu {
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 2;
}

.menu__container {
  display: block;
  width: 100%;
  max-width: 304px;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;

  background-color: var(--menu-background);
  box-shadow: 0px 3px 4px rgba(0, 0, 0, .3);
}

.menu__close-button {
  display: block;
  position: absolute;
  top: 16px;
  left: 16px;
}

.menu__content {
  display: grid;
  width: 100%;
  height: calc(100% - 64px);
  top: 64px;
  left: 0px;
  position: absolute;
  grid-template-rows: auto -webkit-min-content;
  grid-template-rows: auto min-content;
  box-sizing: border-box;
  padding: 16px;
}

.menu__footer {
  text-align: center;
  padding: 16px;
}

.menu__form-group {
  display: grid;
  grid-template-columns: auto -webkit-min-content;
  grid-template-columns: auto min-content;
  height: 30px;
  align-items: center;
}

.menu__form-group input[type=checkbox] {
  height: 20px;
  width: 20px;
}
.score {
  display: grid;
  grid-template-columns: 1fr auto 16px auto 1fr;
}

.score__cell {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.score__img,
.score__number-holder {
  display: flex;
  align-items: center;
  justify-content: center;
}

.score__number-holder {
  display: flex;
  font-size: 24px;
  overflow: hidden;
  margin-top: 8px;
  margin-bottom: 8px;
}

.score__number {
  will-change: transform;
  -webkit-transition: -webkit-transform .3s linear !important;
  transition: -webkit-transform .3s linear !important;
  transition: transform .3s linear !important;
  transition: transform .3s linear, -webkit-transform .3s linear !important;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.score__number--hide {
  -webkit-transform: translateY(150%);
          transform: translateY(150%);
}

.score__number--will-update {
  -webkit-transition: -webkit-transform .1s linear !important;
  transition: -webkit-transform .1s linear !important;
  transition: transform .1s linear !important;
  transition: transform .1s linear, -webkit-transform .1s linear !important;
  opacity: 0;
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%);
}
.cell {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 0px;
  will-change: opacity;
  -webkit-transition: opacity .3s ease-in-out,
    color .3s ease-in-out,
    background-color .3s ease-in-out,
    fill .3s ease-in-out !important;
  transition: opacity .3s ease-in-out,
    color .3s ease-in-out,
    background-color .3s ease-in-out,
    fill .3s ease-in-out !important;
}

.cell:hover {
  cursor: pointer;
}

.cell--opaque {
  opacity: .3;
}
.game-grid {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2px;
  background-color: var(--text-color);
}

.game-grid__cell {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: var(--background-color);
}
.status {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.status__reset-btn {
  font-size: 16px;
  border-radius: 4px;
  height: 30px;
  color: #FFF !important;
  margin-top: 8px;
  background-color: var(--primary-color);
  border: none;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, .3);
  cursor: pointer;
}

.status__reset-btn--red {
  background-color: var(--secondary-color);
}
.game {
  display: grid;
  width: 256px;
  grid-template-rows: 50px 256px 50px;
  grid-gap: 16px;
}
body {
  margin: 0px;
  padding: 0px;
}

.app {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: auto;
  min-height: 100vh;
  position: relative;
  top: 0px;
  left: 0px;
  box-sizing: border-box;
  padding: 32px;

  --background-color: #FFF;
  --text-color: #707070;
  --primary-color: #55B4FA;
  --secondary-color: #F81B81;
  --menu-background: #FFF;

  background-color: var(--background-color);
}

.app,
.app * {
  font-family: 'Arimo', sans-serif;
  color: var(--text-color);

  -webkit-transition: color .3s ease-in-out,
    background-color .3s ease-in-out,
    fill .3s ease-in-out;

  transition: color .3s ease-in-out,
    background-color .3s ease-in-out,
    fill .3s ease-in-out;
}

.app--dark-theme {
  --background-color: #000;
  --text-color: #FFF;
  --menu-background: #131212;
}

.app--no-outlines,
.app--no-outlines * {
  outline: none;
}

.app__menu-button {
  display: block;
  position: fixed;
  top: 16px;
  left: 16px;
  z-index: 1;
}

.app a {
  color: var(--primary-color);
  text-decoration: none;
}
